<style lang=less>
  @import '../../../assets/index.less';
  @import './heigherIdentity.less';
</style>
<template>
  <div class="base-info heigher-identity">
    <!-- 高级认证 -->
    <p class="title">
      <span></span>{{$t('center.heigherIdentity[0]')}}
    </p>
    <el-form  class="heigher-identity-form">
      <!-- 身份证正面照 -->
      <el-form-item :label="$t('center.heigherIdentity[1]')">
        <el-upload
          class="upload-img"
          drag
          :action="baseUrl+imgUpload"
          :before-upload="beforeFrontUpload"
          :on-success="frontUpload"
          :on-change="frontUploadChange"
          :file-list="frontUploadFileList"
          >
          <img class="heigher-identity-img" v-if="!frontUploadUrl" src="../../../assets/img/center/heigher-identity1.png">
          <img class="heigher-identity-upload" v-if="frontUploadUrl" :src="baseUrl+frontUploadUrl">
          <div class="heigher-identity-text">{{$t('center.heigherIdentity[2]')}}</div>
        </el-upload>
      </el-form-item>
      <!-- 身份证背面照 -->
      <el-form-item :label="$t('center.heigherIdentity[3]')">
        <el-upload
          class="upload-img"
          drag
          :action="baseUrl+imgUpload"
          :before-upload="beforeBackUpload"
          :on-success="backUpload"
          :on-change="backUploadChange"
          :file-list="backUploadFileList"
          >
          <img class="heigher-identity-img" v-if="!backUploadUrl" src="../../../assets/img/center/heigher-identity2.png">
          <img class="heigher-identity-upload" v-if="backUploadUrl" :src="baseUrl+backUploadUrl">
          <div class="heigher-identity-text">{{$t('center.heigherIdentity[4]')}}</div>
        </el-upload>
      </el-form-item>
      <!-- 手持身份证照 -->
      <el-form-item :label="$t('center.heigherIdentity[5]')">
        <el-upload
          class="upload-img"
          drag
          :action="baseUrl+imgUpload"
          :before-upload="beforeHoldUpload"
          :on-success="holdUpload"
          :on-change="holdUploadChange"
          :file-list="holdUploadFileList"
          >
          <!-- <i class="el-icon-upload"></i> -->
          <img class="heigher-identity-img" v-if="!holdUploadUrl" src="../../../assets/img/center/heigher-identity3.png">
          <img class="heigher-identity-upload" v-if="holdUploadUrl" :src="baseUrl+holdUploadUrl">
          <div class="heigher-identity-text">{{$t('center.heigherIdentity[6]')}}</div>
        </el-upload>
      </el-form-item>
      <el-button class="submit" type="primary" :loading="submitLoading" @click="submit">{{$t('center.heigherIdentity[7]')}}</el-button>
    </el-form>
  </div>
</template>
<script>
import {setImg} from "@/api/user.js"
export default {
  data () {
    return {
      submitLoading:false, //提交loading
      imgUpload:this.$public.imgUpload, //图片上传地址
      baseUrl:this.$public.baseUrl, //项目域名地址
      frontUploadUrl:'', //图片上传后的回显地址
      backUploadUrl:'', //图片上传后的回显地址
      holdUploadUrl:'', //图片上传后的回显地址
      upLoadImgSize:2, //图片上传大小限制
      upLoadImgType:[
        'image/jpg',
        'image/jpeg',
        'image/png'
      ],
      frontUploadFileList:[],
      backUploadFileList:[],
      holdUploadFileList:[],
    }
  },
  created () {
  },
  watch: {
  },
  methods: {
    beforeFrontUpload(file){//身份证正面照上传---前 用于上传图片的大小各格式限制
      const isLt2M = file.size / 1024 / 1024 < this.upLoadImgSize;
      const isAbleType = this.upLoadImgType.indexOf(file.type) >= 0
      if (!isAbleType) {
        this.$message.error(this.$t('center.heigherIdentity[8]'))
      }
      if (!isLt2M) {
        this.$message.error(this.$t('center.heigherIdentity[9]'))
      }
      return isLt2M && isAbleType
    },
    frontUpload(res) {//身份证正面照上传
      this.frontUploadUrl = res.data.url
    },
    beforeBackUpload(file){//身份证背面照上传---前 用于上传图片的大小各格式限制
      const isLt2M = file.size / 1024 / 1024 < this.upLoadImgSize;
      const isAbleType = this.upLoadImgType.indexOf(file.type) >= 0
      if (!isAbleType) {
        this.$message.error(this.$t('center.heigherIdentity[10]'))
      }
      if (!isLt2M) {
        this.$message.error(this.$t('center.heigherIdentity[11]'))
      }
      return isLt2M && isAbleType
    },
    backUpload(res) {//身份证背面照上传
  　　this.backUploadUrl = res.data.url
    },
    beforeHoldUpload(file){//手持身份证照上传---前 用于上传图片的大小各格式限制
      const isLt2M = file.size / 1024 / 1024 < this.upLoadImgSize;
      const isAbleType = this.upLoadImgType.indexOf(file.type) >= 0
      if (!isAbleType) {
        this.$message.error(this.$t('center.heigherIdentity[10]'))
      }
      if (!isLt2M) {
        this.$message.error(this.$t('center.heigherIdentity[12]'))
      }
      return isLt2M && isAbleType
    },
    holdUpload(res) {//手持身份证照上传
  　　this.holdUploadUrl = res.data.url
    },
    frontUploadChange(file, fileList){ //清上传图片的队列1
      this.frontUploadFileList = fileList.slice(-1)
    },
    backUploadChange(file, fileList){ //清上传图片的队列2
      this.backUploadFileList = fileList.slice(-1)
    },
    holdUploadChange(file, fileList){ //清上传图片的队列3
      this.holdUploadFileList = fileList.slice(-1)
    },
    submit(){//提交
      if(!this.frontUploadUrl){
        this.$public.msg(this.$t('center.heigherIdentity[13]'),'warning',this)
        return
      }
      if(!this.backUploadUrl){
        this.$public.msg(this.$t('center.heigherIdentity[14]'),'warning',this)
        return
      }
      if(!this.holdUploadUrl){
        this.$public.msg(this.$t('center.heigherIdentity[15]'),'warning',this)
        return
      }
      this.submitLoading = true
      let _data = {
        cardimg1: this.frontUploadUrl,
        cardimg2: this.backUploadUrl,
        cardimg3: this.holdUploadUrl
      }
      setImg(_data).then((res)=>{
        this.submitLoading = false
        if(res.data.status == 200){
          this.$public.msg(this.$t('center.heigherIdentity[16]'),'success',this)
          this.$router.back()
        } else{
          this.$public.msg(res.data.msg,'error',this)
        }
      })
    }
  }
}
</script>



